<template>
  <div class="goods-item" @click="itemClick">
      <img v-lazy="showImage" :key="showImage" alt="" @load="imgLoad">
      <div class="goods-info">
        <p>{{goodsItem.title}}</p>
        <span class="price">{{goodsItem.price}}</span>
        <span class="collect">{{goodsItem.cfav}}</span>
      </div>
  </div>
</template>

<script>
export default {
  name: "GoodsListItem",
  props:{
    goodsItem:{
      type:Object,
      default(){
        return {}
      }
    }
  },
  computed:{
    showImage(){
      return this.goodsItem.img || this.goodsItem.image || this.goodsItem.show.img
    }
  },
  methods:{
   /* imgLoad(){
      this.debounce(()=>{
        this.$bus.$emit('imgLoad')
        console.log('send');
      },500)
    },*/
    imgLoad(){
      this.$bus.$emit('imgLoad')
    },
    debounce(fn,wait){
      let timer = null;
      return function(){
        if(timer !== null){
          clearTimeout(timer);
        }
        timer = setTimeout(fn,wait);
      }
    },
    itemClick(){
      const iid = this.goodsItem.iid
      this.$router.push({path: '/detail', query: {iid}})
    }
  }
}
</script>

<style scoped>
  .goods-item {
    padding-bottom: 40px;
    position: relative;
    box-shadow: 0 0 2px #e9e9e9;
  }

  .goods-item img {
    width: 100%;
    border-radius: 3px;
  }

  .goods-info {
    font-size: 12px;
    position: absolute;
    bottom: 5px;
    left: 0;
    right: 0;
    overflow: hidden;
    text-align: center;
  }

  .goods-info p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-bottom: 3px;
  }

  .goods-info .price {
    color: var(--color-high-text);
    margin-right: 20px;
  }

  .goods-info .collect {
    position: relative;
  }

  .goods-info .collect::before {
    content: '';
    position: absolute;
    left: -15px;
    top: -1px;
    width: 14px;
    height: 14px;
    background: url("~assets/img/common/collect.svg") 0 0/14px 14px;
  }
</style>